.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.board {
  display: inline-block;
  margin: 20px;
}

.board-row {
  display: flex;
}

.board-cell {
  width: 150px;
  height: 40px;
  margin: 5px;
  background-color: #e6e6fa;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  font-size: 16px;
}

button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 8px;
  background-color: #87cefa;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #4682b4;
  color: white;
}
